<template>
  <div class="index">
    <!-- 头部 -->
    <Head></Head>
    <!-- 机构管理 -->
    <div class="organization fl-ex fl-co">
      <p style="font-weight:bold; font-size:0.98rem;">机构管理</p>
      <div style="width:17%; height:0.15rem; background-color:#e10000;margin-left:4%;"></div>
    </div>
    <!-- 工会 -->
    <main>
      <router-link class="labor" v-for="(item,index) in list" :key="index" :to="item.path">
        <div class="union">
          <div class="union1 fl-ex al-it">
            <p>{{item.p}}</p>
          </div>
          <div class="union2">
            <div class="line fl-ex al-it ju-be">
              <p>
                上级总工会:
                <span>{{item.span}}</span>
              </p>
              <p>
                级别:
                <span>{{item.flex}}</span>
              </p>
            </div>
            <div class="line fl-ex al-it ju-be">
              <p>
                成立时间:
                <span>{{item.time}}</span>
              </p>
              <p>
                联系人:
                <span>{{item.people}}</span>
              </p>
            </div>
          </div>
        </div>
       </router-link>
    </main>
  </div>
</template>

<script>
import Head from "./head-organization";
export default {
  data() {
    return {
      list: [
        {
          p: "西宁市总共会",
          span: "青海省总工会",
          flex: "一级",
          time: "2018-08-06",
          people: "吴成德",
          path:"/organization-list"
        },
        {
          p: "西宁市总共会",
          span: "青海省总工会",
          flex: "一级",
          time: "2018-08-06",
          people: "吴成德",
          path:"/organization-list"
        },
        {
          p: "西宁市总共会",
          span: "青海省总工会",
          flex: "一级",
          time: "2018-08-06",
          people: "吴成德",
          path:"/organization-list"
        },
        {
          p: "西宁市总共会",
          span: "青海省总工会",
          flex: "一级",
          time: "2018-08-06",
          people: "吴成德",
          path:"/organization-list"
        },
        {
          p: "西宁市总共会",
          span: "青海省总工会",
          flex: "一级",
          time: "2018-08-06",
          people: "吴成德",
          path:"/organization-list"
        },
        {
          p: "西宁市总共会",
          span: "青海省总工会",
          flex: "一级",
          time: "2018-08-06",
          people: "吴成德",
          path:"/organization-list"
        },
      ]
    };
  },
  components: {
    Head
  }
};
</script>

<style scoped>
.index {
  width: 100%;
  height: 100%;
  background-color: #e5e5e5;
}
/* 工会 */
main{
  display: flex;
  flex-direction: column;
  overflow: auto;
  flex: 1;
}
.labor {
  width: 100%;
  height: 7rem;
  background-color: #e2e2e2;
}
.labor .union {
  width: 95%;
  height: 6rem;
  background-color: white;
  margin: 0.5rem 2.5% 0.5rem 2.5%;
  border-radius: 0.4rem;
}
.labor .union .union1 {
  width: 96%;
  height: 2rem;
  margin: 0 auto;
  border-bottom: 0.05rem solid #e7e7e7;
}
.labor .union .union1 p {
  font-size: 0.9rem;
  color: #000000;
}
.labor .union .union2 {
  width: 96%;
  height: 3.9rem;
}
.labor .union .union2 .line {
  width: 100%;
  height: 1.9rem;
}
.labor .union .union2 .line p {
  font-size: 0.8rem;
  color: #a2a2a2;
  margin-left: 2%;
}
</style>



